import React, { useState } from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Carousel from "react-bootstrap/Carousel";
import "./style.css";

function Body() {
  const [activeIndex, setActiveIndex] = useState(0);

  const handleSelect = (selectedIndex, e) => {
    setActiveIndex(selectedIndex);
  };

  const handleIndicatorClick = (index) => {
    setActiveIndex(index);
  };

  return (
    <div className="body">
      <Row>
        <div className="hero">
          <Col sm={3}></Col>
          <Col sm={6}>
            <img
              src={`${process.env.PUBLIC_URL}/bloc_marque.svg`}
              alt="Champagne Haumont &amp; Fils - Depuis 1714 - 3 sicles, 300 millsimes"
              className="img-responsive"
            />
          </Col>
          <Col sm={3}></Col>
        </div>
      </Row>
      <Row>
        <Col sm={12} className="d-flex justify-content-center">
          <Carousel
            activeIndex={activeIndex}
            onSelect={handleSelect}
            interval={3000}
            data-bs-theme="dark"
          >
            <Carousel.Item>
              <img
                className="d-block w-100"
                src={`${process.env.PUBLIC_URL}/slide1.jpg`}
                alt="Ce qui fut autrefois - Haumont & Fils"
              />
            </Carousel.Item>
            <Carousel.Item>
              <img
                className="d-block w-100"
                src={`${process.env.PUBLIC_URL}/slide2.jpg`}
                alt="4 collections  dcouvrir - Haumont & Fils"
              />
            </Carousel.Item>
            <Carousel.Item>
              <img
                className="d-block w-100"
                src={`${process.env.PUBLIC_URL}/slide3.jpg`}
                alt="Entrez dans lunivers - Haumont & Fils"
              />
            </Carousel.Item>
          </Carousel>
        </Col>
      </Row>
      <Row>
        <Col sm={12} className="d-flex justify-content-center">
          <div className="my-carousel-indicators">
            {[0, 1, 2].map((index) => (
              <span
                key={index}
                className={`carousel-indicator ${
                  activeIndex === index ? "active" : ""
                }`}
                onClick={() => handleIndicatorClick(index)}
              >
                {index + 1}
              </span>
            ))}
          </div>
        </Col>
      </Row>
    </div>
  );
}

export default Body;
